import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"

const About = () => (
    <Layout>
        <SEO title="About" />
        <div className="about">
            <label className="title" htmlFor="skills">
                Skills
            </label>
            <div className="row" style={{ paddingBottom: "20px" }}>
                <div className="column-1">
                    <span className="tag">HTML5</span>
                    <span className="tag">CSS3</span>
                    <span className="tag">SASS</span>
                    <span className="tag">jQuery</span>
                    <span className="tag">Bootstrap</span>
                    <span className="tag">Materialize</span>
                    <span className="tag">Responsive Design</span>
                    <span className="tag">UI/UX</span>
                    <span className="tag">Illustrator</span>
                    <span className="tag">Photoshop</span>
                    <span className="tag">SEO</span>
                    <span className="tag">Gulp</span>
                    <span className="tag">Vue</span>
                    <span className="tag">Framework7</span>
                    <span className="tag">React</span>
                    <span className="tag">Redux</span>
                    <span className="tag">Gatsby</span>
                    <span className="tag">GraphQL</span>
                    <span className="tag">Git</span>
                </div>
            </div>
            <label className="title" htmlFor="work-experience">
                Work Experience
            </label>
            <div className="row">
                <div className="column-2">
                    <div className="box">
                        <span className="position">Front-End Developer</span>
                        <span className="company">Pajouhesh Novin Rayaneh</span>
                        <span className="date">2019 – Present</span>
                    </div>
                </div>
                <div className="column-2">
                    <div className="box">
                        <span className="position">Front-End Developer</span>
                        <span className="company">
                            Tivan Enterpreneurship Club
                        </span>
                        <span className="date">2018 – 2019</span>
                    </div>
                </div>
                <div className="column-2">
                    <div className="box">
                        <span className="position">Front-End Developer</span>
                        <span className="company">Pouya Samaneh Diva</span>
                        <span className="date">2017 – 2018</span>
                    </div>
                </div>
                <div className="column-2">
                    <div className="box">
                        <span className="position">Front-End Designer</span>
                        <span className="company">
                            Co-Founder of WebGraphy Team
                        </span>
                        <span className="date">2012 – 2016</span>
                    </div>
                </div>
            </div>
            <label className="title" htmlFor="education">
                Education
            </label>
            <div className="row">
                <div className="column-2">
                    <div className="box">
                        <span className="position">IT Engineering</span>
                        <span className="company">
                            University of Mazandaran
                        </span>
                        <span className="date">2010 – 2014</span>
                    </div>
                </div>
            </div>
        </div>
    </Layout>
)
export default About
